
import Confirm from 'vux/src/components/confirm/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { JDGetIncomingMessage, JDSigning, JDAbnormalUpload } from "@/api/getData";
import $ from 'jquery'
import { mapState } from 'vuex'
export default {
data() {
return {
dialogImageUrl: "",
dialogVisible: false,
IncomingMessage: {},
Items: [],
Picturelist: [],
showCheck: false,
showException: false,
UserID:""
};
},
computed:{
...mapState({
userInfo: state => state.mutations.userInfo
})
},
directives: {
TransferDom
},
components: {
Confirm,
XButton,
Tabbar,
TabbarItem
},
mounted() {
this.getIncomingMessage();
},
methods: {
async getIncomingMessage() {
let data = await JDGetIncomingMessage({
FInterID: this.$route.query.FInterID
});
this.Items = data.Items;
this.Picturelist = data.Picturelist;
this.IncomingMessage = data;
console.log(data);
},
check ( ){
this.showCheck=true
},
exception ( ){
this.showException=true
},
onCancel () {
console.log('on cancel')
},
onConfirmCheck (msg) {
let data = JDSigning({
FInterID: this.$route.query.FInterID,
UserID: this.userInfo[0].FUserID
})
console.log('onConfirmCheck')
if (msg) {
alert(msg)
}
this.$router.push({
path: "/auditForm/inspectionWait"
});

},
onHide () {
console.log('on hide')
},
onShowCheck () {
console.log('onShowCheck')
},
onConfirmException (msg) {
console.log('onConfirmException')
if (msg) {
let data = JDAbnormalUpload({
FInterID: this.$route.query.FInterID,
ExceptionInformation: msg,
UserID: this.userInfo[0].FUserID
})
console.info(eval(data))
this.$router.push({
path: "/auditForm/inspectionWait"
});
}
},
onShowException () {
console.log('onShowException')
}
}
};



